import React from 'react';
import { string, shape } from 'react-proptypes';

import styles from './ExtraBlock.module.scss';

const ExtraBlock = ({ params }) => (
    <div className={styles.extraBlock__container}>
        <h2>
            {params.title}
        </h2>

        <p className={`${styles.extraBlock__description} subline2`}>
            {params.description}
        </p>

        {
            params.action && (
                <a className={`${extraBlock__action} button-text-large`}
                    href="http://saleshero.ai/">
                    {params.action}
                </a>
            )
        }

    </div>
);

ExtraBlock.propTypes = {
    params: shape({
       title: string,
       description: string,
       action: string,
    })
};

export default ExtraBlock;



