import React from 'react';
import LandingPageTemplate from '../../templates/Landing/index'

export default class Admin extends React.Component {
    constructor(props) {
        super(props);

        this.removeHead = this.removeHead.bind(this);
        this.setupCMS = this.setupCMS.bind(this);
    }

    setupCMS() {
        window.CMS.registerPreviewStyle('http://fonts.googleapis.com/icon?family=Material+Icons');
        window.CMS.registerPreviewStyle("http://saleshero.ai/admin_preiviews/styles.css");
        window.CMS.registerPreviewTemplate("landingpage", LandingPageTemplate);
    }

    loadFile(type, src) {
        const remoteFile = `https://unpkg.com/netlify-cms@~0.4/dist/${src}`;
        if (type === 'css') {
            let file = document.createElement('link');
            file.rel = 'stylesheet';
            file.type = 'text/css';
            file.href = remoteFile;
            const head = document.getElementsByTagName('head')[0];
            head.appendChild(file);
        } else {
            const cmsScript = document.createElement('script');
            cmsScript.setAttribute('src', remoteFile);
            document.body.appendChild(cmsScript);

            cmsScript.onload = this.setupCMS;
        }
    }

    loadCMS() {
        this.loadFile('css', 'cms.css');
        this.loadFile('script', 'cms.js');
    }

    removeHead() {
        this.removeItem('material-icons', false);
        this.removeItem('material-styles', false);
    }

    removeItem(id, remove) {
        const item = document.getElementById(id);
        if (remove) {
            return item.parentNode.removeChild(item);
        }

        if (item) {
            item.disabled = true;
        }

        return;
    }

    componentDidMount() {
        this.loadCMS();
        this.removeItem('___gatsby', true);
        window.addEventListener('load', this.removeHead);
        // SG: this is a hack we should use a promisse after the object is loaded. 
        // setTimeout(function () {
        //     console.log(window.CMS);
        // }, 2000);
    }

    componentWillUnmount() {
        window.removeEventListener('load', this.removeHead);
    }

    render() {
        return (
            <div />
        )
    }
}


