import React from 'react';

import { Features } from './Features';
import { MainBlock } from './MainBlock';
import { ExtraBlock } from './ExtraBlock';

import styles from './index.module.scss';

const getBlockData = (block) => ({
    title: block.getIn(['data', 'title']),
    description: block.getIn(['data', 'description']),
    buttonText: block.getIn(['data', 'buttonText']),
    inputPlaceholder: block.getIn(['data', 'inputPlaceholder'])
})

const renderNetlifyTemplate = ({ entry, widgetFor, widgetsFor}) => {
    const items = [];
    const featuresData = widgetsFor('features');
    featuresData.getIn(['data', 'items']).map((item) => {
        items.push({
            text: item.get('text'),
            icon: item.get('icon'),
            color: item.get('color')
        });
    });

    const title = entry.getIn(['data', 'title']);
    const mainBlock = getBlockData(widgetsFor('mainBlock'));
    const extraBlock = getBlockData(widgetsFor('extraBlock'));
    const features = {
        title: featuresData.getIn(['data', 'title']),
        items
    };

    return (
        <div className={styles.landing__container} style={{ pointerEvents: 'none' }}>
            <MainBlock params={ mainBlock } previewing />

            <Features params={ features }/>

            <ExtraBlock params={ extraBlock } />

        </div>
    );
}

const renderGatsbyTemplate = ({ data }) => {
    const page = data.markdownRemark;
    const { title, features, mainBlock, extraBlock } = page.frontmatter;

    return (
        <div className={styles.landing__container}>
            <MainBlock params={mainBlock} />

            <Features params={features}/>

            <ExtraBlock params={extraBlock} />

            <div dangerouslySetInnerHTML={{ __html: page.html }} />
        </div>
    );
};

export default (props) => props.data ? renderGatsbyTemplate(props) : renderNetlifyTemplate(props);

export const query = graphql`
    query LandingPageQuery($slug: String!)  {
        markdownRemark(fields: { slug: { eq: $slug } }) {
            html
            frontmatter {
                title
                mainBlock {
                    title
                    description
                    buttonText
                    inputPlaceholder
                }
                extraBlock {
                    description
                    title
                    action
                }
                features {
                    title
                    items {
                        icon
                        color
                        text
                    }
                }
            }
        }
    }
`;



